<template>
  <div>
    <el-form ref="addForm" :rules="rules" :model="dataForm" label-width="150px">
      <el-row>
        <el-col :span="22">
          <el-form-item label="区域代码" prop="areaCode">
            <el-input type="text" v-model="dataForm.areaCode" maxlength='15' placeholder="请输入区域代码"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="22">
          <el-form-item label="区域名称" prop="areaName">
            <el-input type="text" v-model="dataForm.areaName" maxlength='10' placeholder="请输入区域名称"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="22">
          <el-form-item label="区域类型" prop="areaType">
            <el-input type="text" v-model="dataForm.areaType" maxlength='10' placeholder="请输入区域类型"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="22">
          <el-form-item label="区域状态" prop="areaStatus">
            <el-radio-group v-model="dataForm.areaStatus">
              <el-radio :label="0">停用</el-radio>
              <el-radio :label="1">启用</el-radio>
            </el-radio-group>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="22">
          <el-form-item label="上级菜单" prop="areaPid">
            <AreaTreeSelect :defaultOptions="dataForm.areaPid" @TreeSelectCall="treeSelectCall"></AreaTreeSelect>
          </el-form-item>
        </el-col>
      </el-row>
      <!-- <el-row>
                <el-col :span="22">
                    <el-form-item label="区域级别" prop="areaLevel">
                        <el-select v-model="dataForm.areaLevel">
                            <el-option
                                v-for="item in areaLevel"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row> -->
      <div class="dialog-footer">
        <el-button type="primary" @click="saveArea()">保存</el-button>
        <el-button type="danger" @click="cancle()">取消</el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
import AreaTreeSelect from './AreaTreeSelect.vue'
export default {
  name: 'add-area-dialog',
  components: {
    AreaTreeSelect
  },
  props: [
    'checkedData',
    'isEdit'//true：编辑，false：添加
  ],
  data () {
    return {
      //表单验证
      rules: {
        areaCode: [{
          required: true,
          message: '请输入区域代码',
          trigger: 'change'
        }],
        areaName: [{
          required: true,
          message: '请输入区域名称',
          trigger: 'change'
        }],
        areaType: [{
          required: true,
          message: '请输入区域类型',
          trigger: 'change'
        }],
      },
      dataForm: {
        areaId: "",
        areaCode: "",
        areaName: "",
        areaType: "",
        areaStatus: 0,
        areaPid: "",
        areaLevel: '',
      },
    }
  },
  created () {
    this.setFormData();
  },
  methods: {
    setFormData () {
      if (this.isEdit === false) {
        this.dataForm.areaId = this.checkedData.areaId
        this.dataForm.areaCode = this.checkedData.areaCode
        this.dataForm.areaName = this.checkedData.areaName
        this.dataForm.areaStatus = Number(this.checkedData.areaStatus)
        this.dataForm.areaType = this.checkedData.areaType
        this.dataForm.areaPid = this.checkedData.areaPid
        this.dataForm.areaLevel = this.checkedData.areaLevel
      } else {

      }
    },
    //保存
    saveArea () {
      this.$refs['addForm'].validate((valid) => {
        if (!valid) {
          return false;
        }
        this.$http({
          url: this.$http.adornUrl(this.API.savaOrUpdateArea),
          method: 'post',
          data: this.$http.adornData(
            this.dataForm
          )
        }).then((res) => {
          var resData = res.data;
          if (resData.code == 0) {
            this.$message({
              message: '成功',
              type: 'success'
            });
            this.$emit("addback", true);
          } else {
            this.$message.error(resData.msg);
          }
        })
      });
    },
    /**
    * 上级菜单选择
    */
    treeSelectCall (returnObj) {
      if (returnObj && returnObj.areaId) {
        this.dataForm.areaPid = returnObj.areaId;
        this.dataForm.areaLevel = parseInt(returnObj.areaLevel) + 1;
      }
    },

    //取消
    cancle () {
      this.$emit("addback", false);
    }
  }
}
</script>

<style>
</style>



